* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}


body {
  overflow-x: hidden;
}

.container {
  padding: 40px 0.3rem 0.5rem 0.3rem;
}

.routes_container {
  padding: 1.25rem;
  padding-top: 1rem;
  text-align: justify;
  background-image: linear-gradient(140deg, #eeddd9b2 0%, #e2eca9b6 75%);
}


.txt_align_center {
  text-align: center
}

.contatc_form_container {
  padding: 1rem;
  background: #d3d3d3c4;
  border-radius: 10px;
  border: 1px solid darkgray;

}

.bod_member_container>div {
  display: flex;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.sticky_top_H_NAME {
  background-color: #c0c0c080;
  position: -webkit-sticky;
  position: sticky;
  top: 35px;
  width: 100%;
  border: 2px dotted white;
  z-index: 100;
  display: inline-block;
  text-align: initial;
}

.horizontal_line {
  height: 5px;
  border: none;
  background: rgba(30, 124, 143, 1);
  background: radial-gradient(circle, rgba(255, 29, 0, 1) 3%, rgba(30, 124, 143, 1) 15%, rgba(170, 35, 35, 1) 35%, rgba(30, 124, 143, 1) 60%, rgba(143, 30, 30, 1) 90%, rgba(30, 143, 134, 1) 93%);
}

@media screen and (max-width: 680px) {


  .services_items_container {
    display: grid;
    justify-content: center;

  }

}
.menuContaner {
    height: 35px;
    background: linear-gradient(-45deg, #0f733b, #294aa5);
    color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;

    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;


    background: linear-gradient(130deg, #0f733b, #294aa5, #0f733b);
    background-size: 700% 700%;
    -webkit-animation: gradient 5s linear infinite;
            animation: gradient 5s linear infinite;
}

.menuContaner>a>li {
    list-style: none;
    margin-right: 8px;
    margin-left: 8px;
    cursor: pointer;
    padding: 0.2rem;
    color: white;
}

.menuContaner>a {
    border: 1px solid #dbc481b3;
    border-radius: 7px;
    margin-right: 10px;
}

.menuContaner {
    padding-right: 2.5rem;
}

a {
    text-decoration: none;
}







@-webkit-keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 50% 100%;
    }

    100% {
        background-position: 0% 50%;
    }
}







@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 50% 100%;
    }

    100% {
        background-position: 0% 50%;
    }
}





@media screen and (max-width: 1080px) {

    .banner,
    .banner_contactInfo {
        display: block;
        max-width: 100%;
    }

    .banner {
        padding-bottom: 0.2rem;
        border-bottom: 1px dashed red;
    }

    .banner_contactInfo {
        max-width: 100%;
        display: flex;
        border: none;
        padding-top: 0.5rem;
        justify-content: center;
    }

    .banner_contactInfo>ul>li {
        display: inline-block;
    }



    .menuContaner {
        justify-content: center;
        padding-right: 0;
    }

    .menuContaner>a>li {
        margin-left: 5px;
        margin-right: 5px;
    }

    .footerContainer>div {
        flex-basis: 49%;
        margin-bottom: 3px;
    }

}

@media screen and (max-width: 580px) {

    .menuContaner>a>li {
        margin: 0px;
    }

    .footerContainer>div {
        flex-basis: 100%;
        margin-bottom: 3px;
    }

}
.card__container {
  font-weight: 100;
  display: inline-block;
  width: 350px;
  position: relative;
  top: 0;
  height: 200px;
  overflow: scroll;

  padding: 1rem;
  margin-bottom: 20px;
  border-radius: 20px;
  background: linear-gradient(to right, #eee, #eef);
  
  margin-right: 20px;
}

.card__container:hover {
  box-shadow: grey 3px 3px 3px 2px;
}

.styleLocationLabel {
  color: #8294aa;
  font-size: 14px;
  margin: 4px 0;
}

